  padding: 0;
  font-family: "Montserrat", sans-serif;
  background-color: #f6f9fc;
  color: #667;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

a {
  text-decoration: none;
  outline: none;
}

.container {
