  position: relative;
  max-width: 580px;
  width: 100%;
  margin: -100px auto 0 auto;
}

#footer {
  width: 100%;
  line-height: 60px;
  font-size: 12px;
  text-align: center;
  color: #888;
}

#footer a {
  color: #000;
}



